function sliderCases() {
    var swiper = new Swiper('.cases .mySwiper', {
        slidesPerView: 2,
        spaceBetween: 20,
        navigation: {
            nextEl: ".cases .swiper-button-next",
            prevEl: ".cases .swiper-button-prev",
        },
        pagination: {
            el: ".cases .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            769: {
                slidesPerView: 1.5,
            },
            993: {
                slidesPerView: 2,
            }
        }
    })
}

function sliderCalculations() {
    var swiper = new Swiper('.calculations .mySwiper', {
        slidesPerView: "3",
        spaceBetween: 10,
        navigation: {
            nextEl: ".calculations .swiper-button-next",
            prevEl: ".calculations .swiper-button-prev",
        },
        pagination: {
            el: ".calculations .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: "1.2",
            },
            401: {
                slidesPerView: "1.5",
            },
            451: {
                slidesPerView: "2",
            },
            769: {
                slidesPerView: "3",
            },
            993: {
                slidesPerView: "2",
            },
            1201: {
                slidesPerView: "3",
            }
        }
    })
}

$(document).ready(function () {
    sliderCases();
    sliderCalculations();

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__wrap').toggleClass('header__wrap--active')
        $('body').toggleClass('fixed-body')
    })

    // Scroll-banner
    var $element = $('.tariffs-banner'),
        section = $(".scroll-banner");

    $(window).scroll(function() {
        var scroll = $(window).scrollTop() + $(window).height();
        var offset = section.offset().top;
        if (scroll > offset) {
            $('.tariffs-banner').addClass("tariffs-banner--static");
        } else {
            $('.tariffs-banner').removeClass("tariffs-banner--static");
        }
    });

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active")
        $(this).siblings(".item-hidden").slideToggle()
    }));

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Copy-text
    $('.promo-code__btn').click(function() {
        var text = $(this).siblings('.promo-code__desc').find('.promo-code__text').text();
        var input = $('<textarea>').val(text).appendTo('body').select();
        document.execCommand('copy');
        input.remove();
    });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})
